// Importerar nÃ¶dvÃ¤ndiga funktioner frÃ¥n Vue Router
import { createRouter, createWebHashHistory } from 'vue-router'

// Importera komponenter som motsvarar varje vy/sida
import HomeView from '../views/HomeView.vue'                // Startsida
import ProjectsView from '../views/ProjectsView.vue'        // Projekt-sida
import CertificationsView from '@/views/CertificationsView.vue' // Certifikat-sida

import ContactsView from '@/views/ContactView.vue' // Kontakt-sida


// Definierar de olika rutterna i applikationen
const routes = [
  {
    path: '/',                        // URL-vÃ¤gen fÃ¶r startsidan
    name: 'home',                     // Namn pÃ¥ rutten (kan anvÃ¤ndas fÃ¶r navigation)
    component: HomeView               // Komponent som ska visas
  },
  {
    path: '/projects',                // URL-vÃ¤gen fÃ¶r projektsidan
    name: 'projects',                 // Namn pÃ¥ rutten
    component: ProjectsView           // Komponent som ska visas
  },
  {
  path: '/certifications',                // URL-vÃ¤gen fÃ¶r projektsidan
  name: 'certifications',                 // Namn pÃ¥ rutten
  component: CertificationsView           // Komponent som ska visas
},
 
   {
 
       path: '/contacts',         // URL-vÃ¤gen fÃ¶r certifikat-sidan
       name: 'contacts',          // Namn pÃ¥ rutten
       component: ContactsView     // Komponent som ska visas
 
     }
]

// Skapar en router-instans och anvÃ¤nd hash-baserad navigation
const router = createRouter({
  history: createWebHashHistory(),   // AnvÃ¤nd hash-baserad historik (ex. #/projects)
  routes                              // Tilldela rutterna vi definierade ovan
})

// Exportera router-instansen sÃ¥ att den kan anvÃ¤ndas i hela appen (t.ex. i main.js)
export default router
